import React, { useEffect, useState } from 'react';
import { Route, Routes } from "react-router-dom";
import { AppContainer } from "./styles/styles";
import GlobalStyles from "./styles/globalStyles";
import RanksPage from "./pages/RanksPage";
import HomePage from "./pages/HomePage";
import { ToastContainer } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import AiGodPage from "./pages/AiGodPage";
import NotFoundPage from "./pages/NotFoundPage";
import TasksPage from "./pages/TasksPage";
import LeaderboardPage from "./pages/LeaderboardPage";
import FrensPage from "./pages/FrensPage";
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";
import CountrySelector from "./components/countrySelector";
import SecureRoute from "./routes/SecureRoute";
import { useRewardStore } from "./store/rewardStore";
import Popup from "./components/Popup";
import {userStore} from "./store/uiStore";
import {initDataStore} from "./store/initDataStore";

const queryClient = new QueryClient();

const App: React.FC = () => {
    const rewards = useRewardStore(state => state.rewards);
    const fetchRewards = useRewardStore(state => state.fetchRewards);
    const claimRewards = useRewardStore(state => state.claimRewards);
    const [isRewardPopupVisible, setIsRewardPopupVisible] = useState(false);
    const [popupRewards, setPopupRewards] = useState<typeof rewards>([]); //     

    const updateUiWithTelegramData = initDataStore((state) => state.updateUiWithTelegramData);

    useEffect(() => {
        updateUiWithTelegramData();
    }, []); //

    useEffect(() => {
        fetchRewards();

        if (rewards.length > 0) {
            setPopupRewards(rewards);  //     
            setIsRewardPopupVisible(true);
        }
    }, [rewards]);

    const handleClosePopup = () => {
        claimRewards();
        setIsRewardPopupVisible(false);
    };

    return (
        <QueryClientProvider client={queryClient}>
            <GlobalStyles />
            <AppContainer>
                <Routes>
                    <Route element={<SecureRoute />}>
                        <Route path="/" element={<HomePage />} />
                        <Route path="/country-select" element={<CountrySelector />} />
                        <Route path="/ranks" element={<RanksPage />} />
                        <Route path="/aiGod" element={<AiGodPage />} />
                        <Route path="/tasks" element={<TasksPage />} />
                        <Route path="/leaderboard" element={<LeaderboardPage />} />
                        <Route path="/frens" element={<FrensPage />} />
                    </Route>
                    <Route path="*" element={<NotFoundPage />} />
                </Routes>

                {isRewardPopupVisible && (
                    <Popup
                        onClose={handleClosePopup}
                        rewards={popupRewards}  //   
                    />
                )}
            </AppContainer>
            <ToastContainer
                position="top-center"
                autoClose={3000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="dark"
            />
        </QueryClientProvider>
    );
};

export default App;
