import {create} from 'zustand';
import { claimRewards, getRewardsList} from "../api/api";

export interface Reward {
    id: number;
    type: 'referral' | 'ai_question';
    amount: number;
    inspirations: number;
    replenishments: number;
}

interface RewardResponseData {
    rewards: Reward[];
}

interface RewardResponseMessage {
    status: string;
    text: string;
}

export interface RewardResponse {
    data: RewardResponseData;
    message: RewardResponseMessage;
}

interface RewardStoreState {
    rewards: Reward[];
    fetchRewards: () => Promise<void>;
    claimRewards: () => void
}


//  Zustand store
export const useRewardStore = create<RewardStoreState>((set, get) => ({
    rewards: [
        {
            id: 1,
            type: "referral",
            amount: 500,
            inspirations: 0,
            replenishments: 0
        },
        {
            id: 3,
            type: "ai_question",
            amount: 0,
            inspirations: 1,
            replenishments: 1
        }
    ], //     

    //     (  API)
    fetchRewards: async () => {
        try {
            const response = await getRewardsList();
            if (response) {
                //    
                set({rewards: response.data.rewards});
            }
        } catch (error) {
            console.error('   :', error);
            //      ,   
        }
    },

    claimRewards: async () => {
        await claimRewards()
        set({rewards: []});
    }
}));


