import axios from 'axios';
import { initDataStore } from "../store/initDataStore";
import {toast} from "react-toastify";
import {RewardResponse} from "../store/rewardStore";

const proxyUrl = "https://cors-anywhere.herokuapp.com/";
const targetUrl = "http://147.45.232.159:180/api";
const API_URL = proxyUrl + targetUrl;

const getHeaders = () => {
    const initData = initDataStore.getState().initData;
    return {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'X-Telegram-Init-Data': initData,
    }
}

export const syncClicks = async (clicks: number) => {
    //     
    const headers = getHeaders()

    console.log(headers);

    try {
        const response = await axios.patch(`${API_URL}/user/sync_clicks`,
            { clicks },
            { headers: headers }
        );

        console.log(' :', response.data);
    } catch (error) {
        console.error('  :', error);
        toast.error(`${error}`, {
            position: "top-center",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "dark",
        });
    }
};

export const startMining = async () => {
    const headers = getHeaders()

    try {
        // :    POST     ,    .
        const response = await axios.post(`${API_URL}/mining/start`, {}, { headers });
        console.log('  :', response.data);
        return response.data;
    } catch (error: any) {
        console.error('  :', error);
        toast.error(error.response?.data?.message.text || '  ', {
            position: "top-center",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "dark",
        });
    }
};

