import {create} from 'zustand';
import {startMining} from "../api/api";

interface Reward {
    type: string; //   (, "", ""  ..)
    amount: number; //  
}

//   
interface MiningState {
    isMining: boolean; // , ,   
    timeLeft: number; //    
    nextMining: string;
    reward: Reward | null; //  
    startMining: () => void; //    
    updateTimer: () => void; //    
    resetMining: () => void; //   
    claimReward: () => void; //    
}

// 
const TIMER_IN_SECONDS = 60 * 60 * 8;

const timeLeft = (nextMiningTime: string) => {
    const targetDateStr = ''; //   
    const targetDate = new Date(targetDateStr); //     Date

    const currentDate = new Date(); //  

//    
    const diffInMs = targetDate.getTime() - currentDate.getTime();

//    ,   
    const diffInSeconds = Math.floor(diffInMs / 1000);
    const hours = Math.floor(diffInSeconds / 3600);
    const minutes = Math.floor((diffInSeconds % 3600) / 60);
    const seconds = diffInSeconds % 60;
}

//  zustand store  
export const useMiningStore = create<MiningState>((set, get) => ({
    isMining: false,
    timeLeft: TIMER_IN_SECONDS,
    reward: null, //
    nextMining: '',//   

    startMining: async () => {

        const startMiningResponse = await startMining()

        if (startMiningResponse?.message.status === 'Error') {
            return //    
        }

        if (!get().isMining) {
            set({ isMining: true, timeLeft: TIMER_IN_SECONDS });

            const timerInterval = setInterval(() => {
                const { timeLeft, updateTimer } = get();
                if (timeLeft > 0) {
                    updateTimer();
                } else {
                    clearInterval(timerInterval);
                    //  ,   
                    set({ reward: { type: 'exampleReward', amount: 100 } });
                }
            }, 1000);
        }
    },

    updateTimer: () => {
        set((state) => ({ timeLeft: state.timeLeft - 1 }));
    },

    resetMining: () => {
        set({
            isMining: false,
            timeLeft: TIMER_IN_SECONDS,
            reward: null //  
        });
    },

    claimReward: () => {
        const { reward } = get();
        if (reward) {
            //     (,   )
            console.log(`Claimed reward: ${reward.amount} ${reward.type}`);
            //    
            set({ reward: null, isMining: false });
        } else {
            console.log('No reward to claim');
        }
    },
}));