import {create} from "zustand";
import axios from "axios";
import {toast} from "react-toastify";
import {userStore} from "./uiStore";

const proxyUrl = "https://cors-anywhere.herokuapp.com/";
const targetUrl = "http://147.45.232.159:180/api";
const API_URL = proxyUrl + targetUrl;

interface RankVisibility {
    id: number;
    visibility: {
        id: number;
        type: string;
    };
}

interface Rank {
    id: number;
    league: number;
    name: string;
    press_force: number;
    max_energy: number;
    energy_per_sec: number;
    price: number;
    rank_visibilities: RankVisibility[];
}

interface Activity {
    id: number;
    reg_date: string;
    last_login_date: string;
    last_daily_reward: string;
    last_sync_energy: string;
    next_inspiration: string;
    next_mining: string;
    is_active_mining: boolean;
    active_days: number;
}

interface Reward {
    id: number;
    type_name: string;
    amount: number;
    inspirations: number;
    replenishments: number;
}

interface Stats {
    id: number;
    coins: number;
    energy: number;
    earned_week_coins: number;
    invited_friends: number;
    inspirations: number;
    replenishments: number;
}

export interface User {
    id: number;
    rank: Rank;
    referrer: string | null;
    country: string;
    referral_code: string;
    activity: Activity;
    leader_place: number | null;
    rewards: Reward[];
    stats: Stats;
    user_tasks: any[];

    first_name: string,
    last_name: string,
    username: string,
    language_code: string,
    allows_write_to_pm: boolean
}

export interface UserProfileResponse {
    data: User;
    message: {
        status: string;
        text: string;
    };
    body?: string;
}

interface UserResponse {
    user: {
        id: number;
        first_name: string;
        last_name: string;
        username: string;
        language_code: string;
        allows_write_to_pm: boolean;
    };
}

interface StoreState {
    initData: string | null;
    user: User | null;

    // actions
    updateUiWithTelegramData: () => Promise<void>;
    // fetchUserProfile: () => Promise<void>;
}

export const initDataStore = create<StoreState>((set, get) => ({
    initData: 'null',
    user: null,

    //  initData
    updateUiWithTelegramData: async () => {
        if (window.Telegram && window.Telegram.WebApp) {

            const initData = window.Telegram.WebApp.initData;
            const telegramUser = window.Telegram.WebApp.initDataUnsafe.user;


            if (initData) {
                console.log("initData", initData);
                set({initData});

                const response = await axios.get<UserProfileResponse>(`${API_URL}/user/profile`, {
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json',
                        'X-Telegram-Init-Data': initData,
                    },
                });

                const responseData = response.data.body
                    ? JSON.parse(response.data.body).data
                    : response.data.data;

                const dataToUpdate = {...responseData, telegramUser}

                userStore.getState().updateUiUserData(dataToUpdate);

            } else {
                console.error('initData is not available.');
            }
        } else {
            console.error('Telegram WebApp is not available.');
        }
    },


}));
