import {create} from 'zustand';


import {useAllUsers, User} from "./allUsersStore";



interface Season {
    id: string;
    startDate: string;
    endDate: string;
}

interface LeaderboardState {
    seasons: Season[];
    currentSeasonIndex: number;
    users: User[];
    currentUser: User | null;
    setCurrentSeason: (index: number) => void;
    setUsers: (users: User[]) => void;
    setCurrentUser: (user: User) => void;
}

export const useLeaderboardStore = create<LeaderboardState>((set) => ({
    seasons: [
        {id: '1', startDate: '01 Jul. 2024', endDate: '01 Aug. 2024'},
        {id: '2', startDate: '01 Aug. 2024', endDate: '01 Sep. 2024'},
    ],
    currentSeasonIndex: 0,
    users: useAllUsers.getState().users,
    currentUser: null,
    setCurrentSeason: (index) => set({currentSeasonIndex: index}),
    setUsers: (users) => set({users}),
    setCurrentUser: (user) => set({currentUser: user}),
}));
